<template>
<h1></h1>
<div id="version">
 {{taxYear2}} <br/>
<a href="https://github.com/leeyc0/hksalariestax/wiki"></a> <a href="https://github.com/leeyc0/hksalariestax/">Source code</a>
</div>
<div id="formLayout">
  <div id="autocalculate" class="border">
    
    <table>
      <tbody>
        <tr><td></td><td><input type="number" min="0" v-model.number="totalSiblings" /></td></tr>
        <tr><td><br>{{taxYear2}}</td><td><input type="number" min="0" v-model.number="totalSiblings18" /></td></tr>
        <tr><td></td><td><input type="number" min="0" v-model.number="totalDisabledSiblings" /></td></tr>
      </tbody>
    </table>
    <button type="button" @click="optimizeTax()"></button>
  </div>
  <div id="taxpayerform" class="border">
    
    <button type="button" @click="addTaxPayer()"></button>
    <button type="button" @click="computeTax()"></button>
    <br/>
    <div v-for="[i, taxPayer] of taxPayerMap" :key="i" class="taxPayerInput">
      <div>
        <input size="12" v-model="taxPayer.name" /><br />
         <input size="10" readonly :value="taxPayerResult.get(i) === undefined ? '0' : formatNumber(taxPayerResult.get(i).taxPayable)" />
        <button type="button" @click="openTaxPayerButton(i)"></button>
        <button type="button" @click="deleteTaxPayer(i)"></button><br/>
        
        <input v-model="taxPayer.siblings" type="number" min="0" />
        
        <input v-model="taxPayer.disabledSiblings" type="number" min="0" />
        <br />
        {{taxYear2}}
        <input v-model="taxPayer.siblings18" type="number" min="0" />
        <br />
        
        <div class="flex">
          <div v-for="parent in parentsClaimedByTaxpayer.get(i)" :key="parent.id" class="parentTag">
            {{parent.name}}
          </div>
          <div v-if="parentsClaimedByTaxpayer.get(i).length === 0">
            
          </div>
        </div>
      </div>
    </div>
  </div>
  <div id="parentform" class="border">
    
    <button type="button" @click="addParent()"></button><br/>
    <div v-for="[i, parent] of parentMap" :key="i" class="parentInput">
      <div>
        <input v-model="parent.name" />
        <select v-model="parent.age">
          <option :value="0"></option>
          <optgroup label=""/>
          <option :value="1">{{taxYear1}}54</option>
          <option :value="2">{{taxYear1}}55</option>
          <option :value="3">{{taxYear1}}59</option>
          <option :value="4">{{taxYear1}}60</option>
        </select>
        <button type="button" @click="deleteParent(i)"></button><br/>
      </div>
      <div class="flex">
        <div v-for="[taxPayerId, taxPayer] of taxPayerMap" :key="taxPayerId">
          <label>
            <input type="radio" :name="'parent'.concat(i)" v-model="parent.claimedBy" :value="taxPayerId" /> {{taxPayer.name}}
          </label>
          <label>
            <input type="checkbox" :checked="parent.livingTogether.get(taxPayerId)"
              @change="setParentLivingTogether($event, i, taxPayerId)" /> 
          </label>
        </div>
      </div>
    </div>
  </div>
</div>
<ModalsContainer />
</template>

<style>
.modal-container {
  display: flex;
    align-items: flex-start;
  overflow: auto;
}

.modal-content {
  margin-top: 30px;
  margin-bottom: 30px;
  margin-left: 30px;
  background-color: white;
  padding: 16px;
  border-radius: 4px;
}

.flex {
  display: flex;
  flex-wrap: wrap;
}

div.border {
  border: 1px solid black;
}
</style>

<style scoped>
#formLayout {
  display: grid;
  grid-row-gap: 10px;
  grid-column-gap: 10px;
  grid-template-columns: repeat(auto-fill, 450px);
  max-width: 910px;
}

h1 {
  line-height: 0.5em;
}

#version {
  padding-bottom: 10px;
}

#autocalculate, #taxpayerform, #parentform {
  padding: 10px;
}

#autocalculate {
  grid-column: 1/-1;
}

.taxPayerInput {
  margin-top: 10px;
  margin-bottom: 10px;
}

.parentInput {
  margin-top: 10px;
  margin-bottom: 10px;
}

.parentTag {
  margin-right: 5px;
}

input[type=number] {
  width: 3em;
}
</style>

<script setup>
import { ref } from 'vue'
import { useModal, ModalsContainer } from 'vue-final-modal'
import { useTaxStore } from '@/stores/tax'
import { storeToRefs } from 'pinia'
import taxrule from '@/taxrule.js'
import { autocalculate } from '@/autocalculate.js'
import TaxPayerComponent from '@/taxPayer.vue'
import TaxResultComponent from '@/taxResult.vue'

// component data
const taxYear1 = ref('2025/26')
const taxYear2 = ref('2026/27')
const showTaxPayerIndex = ref(1)
const taxPayerResult = ref(new Map())
const totalSiblings = ref(0)
const totalSiblings18 = ref(0)
const totalDisabledSiblings = ref(0)

const store = useTaxStore()
const { taxPayerMap, parentMap, parentsClaimedByTaxpayer } = storeToRefs(store)

const numberFormatter = new Intl.NumberFormat()

function formatNumber (num) {
  return numberFormatter.format(num)
}

function optimizeTax () {
  autocalculate(store, totalSiblings.value, totalSiblings18.value, totalDisabledSiblings.value)
  computeTax()
}

function computeTax () {
  for (const i of store.taxPayerMap.keys()) {
    const taxResult = taxrule.taxPayable(store.taxPayerMap.get(i), store.parentsClaimedByTaxpayer.get(i))
    taxPayerResult.value.set(i, taxResult)
  }
}

function addTaxPayer () {
  store.addTaxPayer()
  if (isNaN(showTaxPayerIndex.value)) {
    showTaxPayerIndex.value = store.taxPayerMap.keys().next().value
  }
  computeTax()
}

function deleteTaxPayer (i) {
  store.deleteTaxPayer(i)
  taxPayerResult.value.delete(i)
  if (i === showTaxPayerIndex.value) {
    const nextVal = store.taxPayerMap.keys().next()
    if (nextVal.done) {
      showTaxPayerIndex.value = NaN
    } else {
      this.computeTax()
      showTaxPayerIndex.value = nextVal.value
    }
  }
}

function addParent () {
  store.addParent()
}

function deleteParent (i) {
  store.deleteParent(i)
}

function setParentLivingTogether (event, parentId, taxPayerId) {
  store.setParentLivingTogether({ parentId, taxPayerId, livingTogether: event.target.checked })
}

function openTaxPayerButton (i) {
  showTaxPayerIndex.value = i
  const { open: openTaxPayerModal, close: closeTaxPayerModal } = useModal({
    component: TaxPayerComponent,
    attrs: {
      taxYear1,
      taxYear2,
      index: showTaxPayerIndex.value,
      onCloseTaxPayerModal () {
        closeTaxPayerModal()
      },
      onOpenTaxResultModal () {
        computeTax()
        closeTaxPayerModal()
        const { open: openTaxResultModal, close: closeTaxResultModal } = useModal({
          component: TaxResultComponent,
          attrs: {
            taxYear1,
            taxYear2,
            taxResult: taxPayerResult.value.get(showTaxPayerIndex.value),
            onCloseTaxResultModal () {
              closeTaxResultModal()
            },
            onBackToTaxPayerModal () {
              closeTaxResultModal()
              openTaxPayerModal()
            }
          }
        })
        openTaxResultModal()
      }
    }
  })
  openTaxPayerModal()
}

computeTax()
</script>
